@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;600;700;800&display=swap);
* {
    box-sizing: border-box;
}
html {
    font-size: calc(16px + 1vw);
    scroll-behavior: smooth;
}
body {
    margin: 0;
    font-family: 'Public Sans', sans-serif;
    font-weight: 500;
}
ul {
    padding: 0;
    list-style: none;
}
a, li {
    text-decoration: none;
}
span {
    color: #1A9ED8;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 100;
    height: 60px;
    width: 100%;
    padding: 10px 12% 0 12%;
    transition: .3s ease;
    background-color: #333646;
}
.header.hide {
    top: -60px;
}
.logotipo {
    height: 100%;
    margin: 0;
}
.logotipo-img {
    height: 100%;
}
.navigation {
    width: 35%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
}
.navigation-item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.navigation-link {
    height: 50%;
    color:#dddddd;
    font-size: .55rem;
    transition: .3s;
}
.navigation-link::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background-color: #dddddd;
    transition: width .3s;
}
.navigation-link:hover {
    color: white;
}
.navigation-link:hover::after {
    width: 100%;
    transition: width .3s;
}

.hamburguer {
    display: none;
}


@media (max-width: 1040px) {
    .navigation {
        width: 45%;
    }
    .navigation-link {
        font-size: .6rem;
    }
}

@media (max-width: 876px) {
    .navigation {
        width: 55%;
    }
}

@media (max-width: 768px) {
    .header {
        padding: 10px 5% 0 5%;
    }
    .navigation {
        visibility: hidden;
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: absolute;
        z-index: 10;
        top: 0;
        left: 0;
        background-color: #333646;
        margin: 0;
        -webkit-clip-path: circle(100px at 110% -10%);
        clip-path: circle(100px at 110% -10%);
        transition: all 1.5s ease-in-out;
    }
    .navigation.visible {
        visibility: visible;
        -webkit-clip-path: circle(1200px at 110% -10%);
        clip-path: circle(1200px at 110% -10%);
    }
    .navigation-item {
        height: 40px;
    }
    .navigation-link {
        font-size: 1.1rem;
        height: 100%;
    }
    .navigation-link::after {
        height: 1px;
    }
    .hamburguer {
        width: 50px;
        height: 50px;
        padding: 8px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: relative;
        z-index: 20;
        box-sizing: border-box;
        cursor: pointer;
        background-color: #333646;
        border: none;
        outline: none;
    }
    .hamburguer__line {
        width: 100%;
        height: 2px;
        background-color: white;

    }
}

.left {
    position: fixed;
    top: 40%;
    left: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.left__social {
    height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.left__link {
    color: #bbbbbb;
    font-size: .8rem;
    transition: .3s ease;
}
.left__link:hover {
    color: white;
}

.left__line {
    border-left: 1px solid #bbbbbb;
    height: 300px;
}



.right {
    position: fixed;
    top: 40%;
    right: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.right__email {
    font-size: 13px;
    font-weight: 100;
    color: #bbbbbb;
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
    height: 180px;
    margin: 25px 0;
    transition: .3s ease;
}
.right__email:hover {
    color: white;
}

.right__line {
    border-right: 1px solid #bbbbbb;
    height: 300px;
}


@media (max-width: 940px) {
    .left {
        display: none;
    }
    .right {
        display: none;
    }
}
.main {
    height: calc(100vh - 60px);
    display: flex;
    flex-direction: column;
    padding: 45px 12%;
    margin-top: 60px;
    background-color: #252734;
}
.main > p {
    font-size: .6rem;
    margin: 15px 0;
    color: #bbbbbb;
    width: 50%;
    line-height: 1.2rem;
}
.main > h1 {
    margin: 5px 0;
    color: white;
    font-size: 2.2rem;
}
.main > .main__name {
    color: #1A9ED8;
}
.main__button {
    font-size: .6rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: 1px solid #1A9ED8;
    border-radius: 5px;
    background-color: transparent;
    color: #1A9ED8;
    padding: 15px 30px;
    margin-top: 25px;
    transition: .5s all;
}
.main__button:hover {
    background-color: #1A9ED8;
    color: white;
    transform: translateY(-5px);
}

@media (max-width: 1040px) {
    .main {
        padding-top: 0;
        padding-bottom: 0;
    }
    .main > p {
        font-size: .7rem;
        margin: 25px 0;
        width: 75%;
    }
}

@media (max-width: 768px) {
    .main {
        padding-left: 5%;
        padding-right: 5%;
        padding-bottom: 0;
    }
    .main > .main__based {
        width: 65%;
    }
}

@media (max-width: 600px) {
    .main {
        align-items: center;
        padding: 20px;
    }
    .main > * {
        text-align: center;
    }
    .main > .main__based {
        width: 90%;
    }
}


@media (max-width: 420px) {
    .main > p {
        margin: 15px 0; 
    }
}
.about {
    width: 100%;
    display: flex;
    padding: 115px 15%;
    background-color: #252734;
}
.about__text {
    width: 60%;
    margin: 0 15px;
}

.about__title {
    color: white;
    font-size: 1.2rem;
}

.about__text > p {
    color: #bbbbbb;
    font-size: .6rem;
    margin: 40px 0;
}

.about__skills {
    display: flex;
    align-items: center;
}
.about__list {
    margin: 0 80px 0 0;
}
.about__list > li > svg {
    transform: translate(-6px, 6px);
    color:  #1A9ED8;
}

.about__list > li {
    color: #bbbbbb;
    font-size: .45rem;
}
.about__list > li:hover {
    color: white;
}







.about__image {
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 15px;
}
.about__image > div {
    width: 100%;
    border-radius: 50%;
    overflow: hidden;
    
}


.about__image > div > img {
    width: 100%;
    background-color: #1A9ED8;
}



@media screen and (max-width: 1300px) {
    .about {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .about__list {
        margin: 0 70px 0 0;
    }
}

@media screen and (max-width: 1150px) {
    .about__list {
        margin: 0 60px 0 0;
    }
}

@media screen and (max-width: 1040px) {
    .about {
        padding-left: 12%;
        padding-right: 12%;
    }
    .about__list {
        margin: 0 40px 0 0;
    }
}

@media screen and (max-width: 876px) {
    .about {
        padding-left: 8%;
        padding-right: 8%;
    }
    .about__list {
        margin: 0 20px 0 0;
    }
}

@media screen and (max-width: 768px) {
    .about {
        flex-direction: column;
        align-items: center;
        padding: 0 5%;
    }
    .about__text {
        width: 100%;
        margin-bottom: 50px;
    }
    .about__text > p {
        margin: 25px 0;
    }
    .about__image {
        width: 60%;
    }
    .about__list {
        margin: 0;
    }
    .about__skills {
        justify-content: space-around;
    }
}

@media screen and (max-width: 600px) {
    .about__text > * {
        text-align: center;
    }
    .about__image {
        width: 80%;
    }
}

@media screen and (max-width: 420px) {
    .about__image {
        width: 90%;
    }
}
.projects {
    background-color:#252734;
    height: 1000px;
    width: 100%;
}
.contact {
    background-color:#252734;
    height: 1000px;
    width: 100%;
}
